.headerContainer {
  display: flex;
  background-image: url("../assets/header/BRAQ_Social05-04.png");
  background-size: 100%;
  background-repeat: no-repeat;
  justify-content: flex-start;
  flex: 1;
  aspect-ratio: 3.2;
  justify-content: space-between;
}

.discord {
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 24px;
  cursor: pointer;
  font-family: "CaviarDreams";
}

.DiscordContainer {
  width: 25%;
  padding-top: 30px;
  margin-left: 20px;
  justify-content: center;
  align-items: center;
  color: antiquewhite;
  text-decoration-color: black;
}

.socialContainer {
  width: 25%;
  padding-top: 30px;
  margin-right: 30px;
  margin-left: 20px;
  justify-content: flex-end;
}

.twitterLogo {
  max-height: 40px;
}

.instagramLogo {
  max-height: 40px;
  margin-left: 30px;
}

@media only screen and (max-width: 500px) {
  .headerContainer {
    display: flex;
    background-image: url("../assets/header/BRAQ_mobilebackground-05.png");
    background-size: 100%;
    background-repeat: no-repeat;
    justify-content: space-between;
    flex: 1;
    aspect-ratio: 2;
  }
}
@media (max-width: 950px) {
  .headerContainer {
    display: flex;
    background-image: url("../assets/header/BRAQ_mobilebackground-05.png");
    background-size: 100%;
    background-repeat: no-repeat;
    justify-content: flex-start;
    flex: 1;
    aspect-ratio: 2.25;
    justify-content: space-between;
  }
}

@media (max-width: 950px) {
  .discord {
    padding-top: 30px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 14px;
    cursor: pointer;
    font-family: "CaviarDreams";
    max-width: 200px;
  }
}

@media (max-width: 950px) {
  .twitterLogo {
    max-height: 35px;
  }
}
@media (max-width: 950px) {
  .instagramLogo {
    max-height: 35px;
    padding-left: 10px;
  }
}

@media only screen and (max-width: 500px) {
  .discord {
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 14px;
    cursor: pointer;
    font-family: "CaviarDreams";
    max-width: 100px;
  }
}
@media only screen and (max-width: 500px) {
  .DiscordContainer {
    width: 25%;
    padding-top: 20px;
    margin-left: 20px;
    justify-content: center;
    align-items: center;
    color: antiquewhite;
    text-decoration-color: black;
  }
}
@media only screen and (max-width: 400px) {
  .socialContainer {
    width: 25%;
    padding-top: 10px;
    margin-right: 30px;
    margin-left: 20px;
    justify-content: flex-end;
  }
}
@media only screen and (max-width: 500px) {
  .twitterLogo {
    max-height: 25px;
  }
}

@media only screen and (max-width: 500px) {
  .instagramLogo {
    max-height: 25px;
  }
}
